<template>
  <div class="container">
    <Header :title="t('index.社區獎勵')" type="back"></Header>
    <div class="notformed">
      <div class="top-block">
        <div class="left flex-item">
          <div class="title">{{t('index.社區獎勵待領取')}}</div>
          <div class="value">{{earned}} Back </div>
        </div>
        <div class="right">
          <div class="btn-wrap" style="margin:0 0 0 10px;">
            <van-button size="mini" type="primary" class="btn" :loading-text="$t('index.領取')" :loading="btnRecive.loading" :disabled="!Number(earned) || btnRecive.loading" @click="reciveHandle()">{{$t('index.領取')}}</van-button>
          </div>
        </div>

      </div>
      <div class="lists">
        <div class="li">
          <div class="key">{{t('index.開始時間')}}</div>
          <div class="value flex-item">{{datagetOrderInfo.time?$dateformat(datagetOrderInfo.time):t('index.暫未開放')}}</div>
        </div>
        <div class="li">
          <div class="key">{{t('index.縂鎖倉天數')}}</div>
          <div class="value flex-item">{{datagetOrderInfo.unlockPeriod}}{{t('index.天')}}</div>
        </div>
        <div class="li">
          <div class="key">{{t('index.剩餘天數')}}</div>
          <div class="value flex-item">{{datagetOrderInfo.surplusDay}}{{t('index.天')}}</div>
        </div>
        <div class="li border-1px-b"></div>
        <div class="li">
          <div class="key">{{t('index.待釋放')}}</div>
          <div class="value flex-item">{{datagetOrderInfo.isWillRelease}} Back</div>
        </div>
        <div class="li">
          <div class="key">{{t('index.已釋放')}}</div>
          <div class="value flex-item">{{datagetOrderInfo.receiveProfit}} Back</div>
        </div>
        <div class="li">
          <div class="key">{{t('index.進度')}}</div>
          <div class="progress-wrap flex-item">
            <div class="out">
              <div class="in" :style="{'width':datagetOrderInfo.progress}">
                <img class="img" src="@/assets/images/process-btn.png" alt="">
                <!-- <div class="text">{{item.process}}</div> -->
              </div>
            </div>
          </div>
          <div class="text" style="min-width:40px;margin-left:10px;text-align: right;">{{datagetOrderInfo.progress}}</div>
        </div>
      </div>
    </div>
  </div>
</template>
  <script>
import Header from '@/components/Header'
import { reactive, toRefs, computed, watch, getCurrentInstance } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";

import { config } from "@/utils/config";
// import { MaxUint256 } from "@ethersproject/constants";
// import BigNumber from "bignumber.js";
import tokenAbi from "@/utils/abi.json";
import communityAbi from "@/utils/communityAbi.json";
import BigNumber from 'bignumber.js';
import { Toast } from "vant";
Toast.allowMultiple();
export default {
  components: {
    Header
  },
  setup () {
    const { proxy } = getCurrentInstance();
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      communityContract: null,
      backContract: null,
      earned: '0.0000',
      btnRecive: {
        loading: false
      },
      datagetOrderInfo: {
        user: '',
        profit: '--',
        receiveProfit: '--',
        time: '',
        unlockPeriod: '--',
        isWillRelease: '--',
        progress: '0',
        surplusDay: '--'
      },
      initContract: () => {
        data.backContract = new data.web3.eth.Contract(
          tokenAbi,
          config.backAddress
        );
        data.communityContract = new data.web3.eth.Contract(
          communityAbi,
          config.community
        );
        data.initData()
      },
      initData: () => {
        data.userOrderFetch()
        data.earnedGet()
      },
      earnedGet: async () => {
        try {
          let decimalsBACK = await data.backContract.methods.decimals().call()
          let _earned = await data.communityContract.methods.earned(data.address).call({ from: data.address });
          _earned = new BigNumber(_earned).dividedBy(10 ** decimalsBACK).toFixed(4)
          data.earned = _earned
        } catch (error) {
          proxy.$errorformat(error, 119)
        }
      },
      reciveHandle: async () => {
        data.btnRecive.loading = true
        try {
          let _gas = await data.communityContract.methods.takeProfit().estimateGas({
            from: data.address
          })
          console.log('_gas：', _gas, _gas + 200000)
          await data.communityContract.methods.takeProfit().send({
            gas: _gas + 200000,
            gasPrice: 20000000000,
            from: data.address
          })
          Toast(t('Success'))
          data.btnRecive.loading = false
        } catch (error) {
          data.btnRecive.loading = false
          proxy.$errorformat(error)
        }
      },
      userOrderFetch: async () => {
        const ToastUserOrder = Toast.loading({
          duration: 0
        })
        try {
          let userOrder = await data.communityContract.methods.userOrder(data.address).call({
            from: data.address
          })
          console.log('userOrder:', userOrder)
          if (!Number(userOrder)) {
            console.log('无订单-----------')
          } else {
            console.log('有订单-----------')
            await data.getOrderInfo(userOrder)
          }
          ToastUserOrder.clear()
        } catch (error) {
          ToastUserOrder.clear()
          proxy.$errorformat(error, 159)
          console.log(390)
        }
      },
      getOrderInfo: async (orderId) => {
        const ToastgetOrderInfo = Toast.loading({
          duration: 0
        })
        try {
          let _getOrderInfo = await data.communityContract.methods.getOrderInfo(orderId).call({
            from: data.address
          })
          console.log(_getOrderInfo, '_getOrderInfo')
          let decimalsBACK = await data.backContract.methods.decimals().call()
          let unlockTime = await data.communityContract.methods.unlockTime().call()
          let {
            user,
            profit, // 总奖金
            receiveProfit, //已领取奖金
            time,
            unlockPeriod //总期数
          } = _getOrderInfo
          profit = new BigNumber(profit).dividedBy(10 ** decimalsBACK).toFixed(4)
          receiveProfit = new BigNumber(receiveProfit).dividedBy(10 ** decimalsBACK).toFixed(4)
          //   进度条 = （已领取+未领取） / 总奖金
          //   剩余天数 = 总期数 - （curtime - time） / unlockTime
          //   待释放 = 总奖金 - (已领取+未领取)
          // 待释放
          let isWillRelease = new BigNumber(profit).minus(receiveProfit).minus(data.earned).toFixed()
          //   进度
          let progress = new BigNumber(receiveProfit).plus(data.earned).dividedBy(profit).multipliedBy(100).toFixed()
          progress = progress + '%'
          //   剩余天数
          let curTime = Math.floor((new Date()).getTime() / 1000)
          let curDay = new BigNumber(curTime).minus(time).toFixed()
          curDay = new BigNumber(curDay).dividedBy(unlockTime).toFixed(2)
          console.log(new BigNumber(curDay).dividedBy(unlockTime).toFixed(), '2')
          let surplusDay = new BigNumber(unlockPeriod).minus(curDay).toFixed(0)
          data.datagetOrderInfo = {
            user,
            profit,
            receiveProfit,
            time,
            unlockPeriod,
            isWillRelease,
            progress,
            surplusDay
          }
          ToastgetOrderInfo.clear()
        } catch (error) {
          ToastgetOrderInfo.clear()
          proxy.$errorformat(error, 210)
        }
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
  </script>
  <style lang="less" scoped>
.notformed {
  padding: 16px;
  .top-block {
    display: flex;
    align-items: center;
    background: #1a202e;
    border-radius: 8px;
    padding: 12px 12px;
    &:not(:last-child) {
      margin-bottom: 12px;
    }
    .left {
      .title {
        font-size: 15px;
        font-family: PingFang SC;
        font-weight: bold;
        color: #ffffff;
      }
      .value {
        padding-top: 16px;
      }
    }
  }
  .lists {
    min-height: 105px;
    background: #1a202e;
    border-radius: 8px;
    padding: 12px 12px;
    &:not(:last-child) {
      margin-bottom: 12px;
    }
    .li {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      &:not(:last-child) {
        margin-bottom: 10px;
      }
      .title {
        font-size: 13px;
        font-family: PingFang SC;
        font-weight: bold;
        color: #ffffff;
      }
      .btn-wrap {
        .van-button {
          font-size: 13px;
          font-family: PingFang SC;
          font-weight: bold;
          color: #ffffff;
        }
      }
      .key {
        font-size: 13px;
        font-family: PingFang SC;
        font-weight: 500;
        color: #ffffff;
      }
      .value {
        font-size: 13px;
        font-family: PingFang SC;
        font-weight: 500;
        color: #ffffff;
        display: flex;
        align-items: center;
        justify-content: flex-end;

        img {
          width: 16px;
          height: 16px;
          display: inline-block;
          margin-left: 12px;
        }
      }
      .progress-wrap {
        margin-left: 16px;
        .out {
          background: #0e131d;
          border-radius: 5px;
          .in {
            height: 9px;
            background: linear-gradient(0deg, rgba(#2e79fa), rgba(#4fc6e5));
            border-radius: 5px;
            position: relative;
            img {
              position: absolute;
              top: -2.5px;
              right: -7.5px;
              display: block;
              width: 15px;
              height: 15px;
            }
            .text {
              position: absolute;
              top: -2.5px;
              right: -45px;
              font-size: 13px;
              font-family: PingFang SC;
              font-weight: 500;
              color: #ffffff;
              display: inline-block;
            }
          }
        }
      }
    }
  }
}
</style>
  <style lang="less">
#app {
  .notformed {
    .btn-wrap {
      .van-button {
        padding: 0 7px;
        font-size: 13px;
        font-family: PingFang SC;
        font-weight: bold;
        color: #ffffff;
      }
    }
  }
}
</style>
  